import { combineReducers } from 'redux';

const todo = (state, action) => {
	switch (action.type) {
		case 'ADD_TODO':
			return {
						id: action.id,
						text: action.text,
						completed: false
					};

		case 'TOGGLE_TODO':
			return action.id !== state.id ? state
										  : {
												...state,
												completed: !state.completed
											};

		case 'DELETE_TODO':
			return action.id !== state.id;

		default:
			return state;

	}
};

const todos = (state = [], action) => {
	switch (action.type) {
		case 'ADD_TODO':
			return [
						...state,
						todo(undefined, action)
					];

		case 'TOGGLE_TODO':
			return state.map(t => todo(t, action));

		case 'DELETE_TODO':
			return state.filter(t => todo(t, action));

		default:
			return state;

	}
};

const visibilityFilter = (
	state = 'SHOW_ALL',
	action
) => {
	switch (action.type) {
		case 'SET_VISIBILITY_FILTER':
			return action.filter;

		default:
			return state;
	}
}

export const todoAppReducer = combineReducers({
	todos,
	visibilityFilter
});


